import React from "react";
import s from './Users.module.css'
import Paginator from "../../common/Paginator/Paginator";
import User from "./User";
import {UserType} from "../../../types/types";
import {FormSearch} from "./FormSearch";
import {FilterType} from "../../../redax/users-reducer";


type Props = {
    currentPage: number,
    onPageChanged: (pageNumber: number) => void,
    totalUsersCount: number,
    pageSize: number,
    unfollow: (userId: number) => void,
    follow: (userId: number) => void,
    followingInProgress: Array<number>,
    users: Array<UserType>
    onFilterChanged: (filter: FilterType) => void
}

let Users: React.FC<Props> = (props) => {

    return <div className={s.box}>
        <div>
            <FormSearch onFilterChanged={props.onFilterChanged} />
        </div>

        <div className={s.boxPaginator}>
            <Paginator currentPage={props.currentPage} onPageChanged={props.onPageChanged}
                       totalUsersCount={props.totalUsersCount} pageSize={props.pageSize}/>
        </div>
        {
            props.users.map(u => <User key={u.id} user={u} followingInProgress={props.followingInProgress}
                                       unfollow={props.unfollow} follow={props.follow}/>)
        }
    </div>
}

export default Users;

