import * as React from "react";
import {connect} from "react-redux";
import Image from "../../common/Prelouder";
import {useLocation, useNavigate, useParams} from "react-router-dom";
import {compose} from "redux";
import {UserType} from "../../../types/types";
import {AppStateType} from "../../../redax/redux-store";
import Users from "./Users";
import {requestUsers, follow, unfollow, actions, FilterType} from "../../../redax/users-reducer";
import {
    getCurrentPage, getFollowingInProgress, getIsAuth, getIsFetching,
    getPageSize, getFilter, getTotalUsersCount, getUsers
} from "../../../redax/users-selectors";


type MapStateProps = {
    currentPage: number
    pageSize: number
    isFetching: boolean
    totalUsersCount: number
    followingInProgress: Array<number>,
    users: Array<UserType>,
    isAuth: boolean
    filter: FilterType
}
type MapDispatchProps = {
    unfollow: (userId: number) => void,
    follow: (userId: number) => void,
    getUsers: (currentPage: number, pageSize: number, filter: FilterType) => void,
    setCurrentPage: (pageNumber: number) => void
}
type PropsType = MapStateProps & MapDispatchProps

class UsersContainer extends React.Component<PropsType> {

    componentDidMount() {
        const {currentPage, pageSize, filter} = this.props
        this.props.getUsers(currentPage, pageSize, filter)
    };

    onPageChanged = (pageNumber: number) => {
        let {pageSize, filter} = this.props
        this.props.setCurrentPage(pageNumber);
        this.props.getUsers(pageNumber, pageSize, filter)
    }

    onFilterChanged = (filter: FilterType) => {
        let {pageSize} = this.props
        this.props.getUsers(1, pageSize, filter)
    }


    render() {

        let pagesCount = Math.ceil(this.props.totalUsersCount / this.props.pageSize);
        let pages = [];
        for(let i=1; i<=pagesCount; i++){
            pages.push(i)
        };

        return <>
            { this.props.isFetching ? <Image /> : null }

            <Users totalUsersCount={this.props.totalUsersCount}
                   pageSize={this.props.pageSize}
                   currentPage={this.props.currentPage}
                   onPageChanged={this.onPageChanged}
                   onFilterChanged={this.onFilterChanged}
                   unfollow={this.props.unfollow}
                   follow={this.props.follow}
                   users={this.props.users}
                   followingInProgress={this.props.followingInProgress}
                   //userId={ this.props.router.params.userId}
                   //isAuth={ this.props.isAuth }
                   //router={ this.props.router}
            />
        </>
    }
}

let mapStateToProps = (state: AppStateType) => {
    return {
        users: getUsers(state),
        pageSize: getPageSize(state),
        totalUsersCount: getTotalUsersCount(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetching(state),
        followingInProgress: getFollowingInProgress(state),
        isAuth: getIsAuth(state),
        filter: getFilter(state)
    }
}

function withRouter(Component: any) {
    function ComponentWithRouterProp(props: any) {
        let location = useLocation();
        let navigate = useNavigate();
        let params = useParams();
        return (
            <Component
                {...props}
                router={{ location, navigate, params }}
            />
        );
    }
    return ComponentWithRouterProp;
}

export default compose<React.ComponentType>( connect(mapStateToProps,
        { getUsers: requestUsers, unfollow, follow, setCurrentPage: actions.setCurrentPage
        }), withRouter)(UsersContainer)









